<template>
  <div id="tablet">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>人类活动变化检测详情</span>
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
    </div>
    <div class="map-box">
      <div class="topLeft-box" :class="{activeBox: boxstate === 1}">
        <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="mapOpen" size="small" class="boxBtn"></Button>
        <Map :boxstate="boxstate" ref="maps" @layerConClick="layerConClick"></Map>
      </div>
      <div class="topRight-box" :class="{activeBox: boxstate === 2}">
        <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="mapOpen2" size="small" class="boxBtn"></Button>
        <Table :data="allGeometriesArr" :columns="columns2"></Table>
      </div>
      <Card style="width: calc(100% - 30px)" class="bottom-box" id="changeDetect_mapBox" :class="{smallToBig:boxstate === 3}">
        <Button :icon="boxstate === 3 ? 'md-contract' : 'md-expand'" @click="handleTaleBox" size="small" class="boxBtn"></Button>
        <div class="bigBox">
          <div class="smallBox">
            <Table border stripe :loading="mapLoading" :columns="mapColumns1" :data="mapData1" highlight-row>
            </Table>
          </div>
        </div>
      </Card>
      <!-- 查看详情-->
      <div class="detailsTable" :class="{detailsActive:detailsActive}">
        <detailsTable ref="detailsTable" @signOut="detailSignOut"></detailsTable>
      </div>
    </div>
  </div>
</template>
<script>
import detailsTable from './detailsTable'
import Map from './map'
import { getActivityChangeList } from '@/api/remoteSensing'
import config from '@/setting/mapConfig'
let mapData1 = [{
  nyyd: '', // 农业用地
  jmd: '', // 居民点
  gkyd: '', // 工矿用地
  csc: '', // 采石场
  nyss: '', // 能源设施
  lyss: '', // 旅游设施
  jtss: '', // 交通设施
  yzc: '', // 养殖场
  dl: '', // 道路
  qt: '' // 其他人工设施
}, {
  nyyd: '', // 农业用地
  jmd: '', // 居民点
  gkyd: '', // 工矿用地
  csc: '', // 采石场
  nyss: '', // 能源设施
  lyss: '', // 旅游设施
  jtss: '', // 交通设施
  yzc: '', // 养殖场
  dl: '', // 道路
  qt: '' // 其他人工设施
}, {
  nyyd: '', // 农业用地
  jmd: '', // 居民点
  gkyd: '', // 工矿用地
  csc: '', // 采石场
  nyss: '', // 能源设施
  lyss: '', // 旅游设施
  jtss: '', // 交通设施
  yzc: '', // 养殖场
  dl: '', // 道路
  qt: '' // 其他人工设施
}]
export default {
  name: 'checkInfoMap',
  data () {
    return {
      map: {}, // 地图实例
      view: {}, // 地图视图实例
      /* 放大块的标识 */
      boxstate: 0,
      /* 表格数据 */
      mapColumns1: [
        {
          title: '数据',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'name'
        },
        {
          title: '农业用地',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'nyyd'
        },
        {
          title: '居民点',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'jmd'
        },
        {
          title: '工矿用地',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'gkyd'
        },
        {
          title: '采石场',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'csc'
        },
        {
          title: '能源设施',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'nyss'
        },
        {
          title: '旅游设施',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'lyss'
        },
        {
          title: '交通设施',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'jtss'
        },
        {
          title: '养殖场',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'yzc'
        },
        {
          title: '道路',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'dl'
        },
        {
          title: '其他人工设施',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          key: 'qt'
        }
      ],
      /* 加载 */
      mapLoading: false,
      /* 查看详情弹框状态 */
      detailsActive: false,
      /* 需传入详情表格数据 */
      mapData1,
      /* 存储新增的斑块 */
      addGeometriesArr: [],
      /* 存储减少的斑块 */
      subGeometriesArr: [],
      /* 全部斑块数组 */
      allGeometriesArr: [],
      /* 详情中斑块表格 */
      columns2: [{
        title: '活动名称',
        align: 'center',
        ellipsis: true,
        tooltip: true,
        render: (h, params) => {
          let str = ''
          if (params.row.trend) {
            str = params.row.attributes.aa
          } else {
            str = params.row.attributes.name1
          }
          return h('a', {
            style: {
              textDecoration: 'underline',
              color: '#00f'
            },
            on: {
              click: () => {
                this.$refs.maps.toPolygon(params.row.trend, params.row.attributes.index)
              }
            }
          }, str)
        }
      }, {
        title: '类型',
        align: 'center',
        ellipsis: true,
        tooltip: true,
        render: (h, params) => {
          if (params.row.trend) {
            return h('span', params.row.attributes.bb)
          } else {
            return h('span', params.row.attributes.type1)
          }
        }
      }, {
        title: '变化趋势',
        align: 'center',
        ellipsis: true,
        tooltip: true,
        render: (h, params) => {
          return h('span', params.row.trend ? '增加' : '减少')
        }
      }]
    }
  },
  components: {
    // 查看详情
    detailsTable,
    // 原人类活动--地图
    Map
  },
  methods: {
    /* 初始化数据 */
    init (id) {
      this.getMapTableList(id)
    },
    /* 原人类活动---地图框放大缩小开关控制 */
    mapOpen () {
      this.boxstate = this.boxstate === 1 ? 0 : 1
    },
    /* 原人类活动---点击图层控制器打开地图框 */
    layerConClick () {
      this.boxstate = 1
    },
    /* 比对人类活动---地图框放大缩小开关控制 */
    mapOpen2 () {
      this.boxstate = this.boxstate === 2 ? 0 : 2
    },
    /* 获取详情信息 */
    getMapTableList (id) {
      this.mapLoading = true
      getActivityChangeList({
        data: {
          id: id
        }
      }).then(res => {
        this.mapLoading = false
        if (res.data.code === '0000') {
          for (let i = 0; i < this.mapData1.length; i++) {
            switch (i) {
              case 0:
                this.mapData1[i].name = res.data.data.image1.name
                break
              case 1:
                this.mapData1[i].name = res.data.data.image2.name
                break
              case 2:
                this.mapData1[i].name = '变化'
                break
              default:
                break
            }
            this.mapData1[i].nyyd = res.data.data['nyyd' + (i + 1)] // 农业用地
            this.mapData1[i].jmd = res.data.data['jmd' + (i + 1)] // 居民点
            this.mapData1[i].gkyd = res.data.data['gkyd' + (i + 1)] // 工矿用地
            this.mapData1[i].csc = res.data.data['csc' + (i + 1)] // 采石场
            this.mapData1[i].nyss = res.data.data['nyss' + (i + 1)] // 能源设施
            this.mapData1[i].lyss = res.data.data['lyss' + (i + 1)] // 旅游设施
            this.mapData1[i].jtss = res.data.data['jtss' + (i + 1)] // 交通设施
            this.mapData1[i].yzc = res.data.data['yzc' + (i + 1)] // 养殖场
            this.mapData1[i].dl = res.data.data['dl' + (i + 1)] // 道路
            this.mapData1[i].qt = res.data.data['qt' + (i + 1)] // 其他人工设施
          }
          // 处理增加面积图斑的geometry
          this.addGeometriesArr = this.json2geometry(res.data.data.imageContrast.data1, 1)
          // 处理减少面积图斑的geometry
          this.subGeometriesArr = this.json2geometry(res.data.data.imageContrast.data2, 0)
          // 合并数据的数组
          this.allGeometriesArr = this.addGeometriesArr.concat(this.subGeometriesArr)
          // 渲染图层
          this.$refs.maps.renderBankuai(this.addGeometriesArr, this.subGeometriesArr)
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '获取对比详情失败', desc: res.data.msg })
        }
      })
    },
    /* 查看详情页面退出 */
    detailSignOut (addActive) {
      this.detailsActive = addActive
    },
    handleTaleBox () {
      if (this.boxstate === 3) {
        this.boxstate = 0
        this.tableHeight = window.innerHeight - 332
      } else {
        // 放大
        this.boxstate = 3
        this.tableHeight = Math.floor((window.innerHeight - 326) / 2)
      }
    },
    /* json转geometry */
    json2geometry (json, trend) {
      let temparr = JSON.parse(json)
      for (let i = 0; i < temparr.length; i++) {
        temparr[i].attributes = JSON.parse(temparr[i].attributes)
        temparr[i].attributes.index = trend.toString() + '-' + i
        temparr[i].geometry = JSON.parse(temparr[i].geometry)
        temparr[i].geometry.rings = JSON.parse(temparr[i].geometry.rings)
        temparr[i].geometry.spatialReference = {
          wkid: config.mapWkid
        }
        temparr[i].trend = trend
      }
      return temparr
    }
  }
}
</script>
<style scoped lang="scss">
  #tablet{
    position: relative;
    height: 100%;
    overflow: hidden;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
      .title {
        float: left;
        font-size: 14px;
        i {
          vertical-align: middle;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
    .map-box{
      width: 100%;
      height: calc(100% - 80px);
      padding: 0 15px 15px;
      position: relative;
      .topLeft-box {
        position: absolute;
        top: 0;
        left: 15px;
        width: calc(50% - 20px);
        height: calc(70% - 5px);
        border: 1px solid #ccc;
        /* 地图框样式 */
        #viewDiv{
          width: 100%;
          height: 100%;
        }
      }
      .topRight-box {
        position: absolute;
        top: 0;
        right: 15px;
        width: calc(50% - 20px);
        height: calc(70% - 5px);
        border: 1px solid #ccc;
        overflow: auto;
      }
      .bottom-box {
        position: absolute;
        bottom: 0;
        left: 15px;
        width: calc(100% - 30px);
        height: calc(30% - 5px);
        border: 1px solid #ccc;
        overflow: auto;
      }
      .sign-btn {
        position: absolute;
        right: 15px;
        bottom: 0px;
        z-index: 99;
      }
      /* box放大 */
      .activeBox, .smallToBig{
        position: absolute;
        left: 15px !important;
        top: 0 !important;
        width: calc(100% - 30px) !important;
        height: 100% !important;
        z-index: 999;
        border: none;
        transition: all 0.8s ease;
        background: #fff;
      }
      .boxBtn {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 99;
      }
      .detailsTable {
        height: calc(100% + 65px);
        width: 100%;
        position: absolute;
        right: -120%;
        top: -65px;
        z-index: 1356;
        background: #fff;
        transition: all 0.8s ease;
        border-radius: 0 0 5px 5px;
        .closeBtn{
          position: absolute;
          right:10px;
          bottom:10px;
          z-index: 131531313;
        }
      }
      .detailsActive {
        right:0;
      }
      .ivu-card >>> .ivu-card-body {
        padding: 0 !important;
      }
    }
  }
</style>
